.book {
    height: 50rem;
    background:linear-gradient(115deg, rgba(255,255,255,0.9) 0%, rgba(255,255,255,0.9) 58%, rgba(186,133,84,0.4) 58.1%, rgba(161,79,3,0.4) 100%), url(../img/bg-form.jpg) center / cover no-repeat {}
    box-shadow: $box-shadow;
    border-radius: 0.5rem;
    overflow: hidden;
    @include respond(tab-port) {
        background:linear-gradient(115deg, rgba(255,255,255,0.9) 0%, rgba(255,255,255,0.9) 508%, rgba(186,133,84,0.4) 58.1%, rgba(161,79,3,0.4) 100%), url(../img/bg-form.jpg) center / cover no-repeat {} } }

.form {
    width: 65%;
    padding-top: 5rem;
    padding-left: 5rem;
    &__group:not(:last-child) {
        margin-bottom: 2rem; }
    &__group:nth-child(4) {
        margin-bottom: 4rem; }
    @include respond(tab-land) {
        &__group:not(:last-child) {
            margin-bottom: 0; }
        &__group:nth-child(4) {
            margin-bottom: 2rem; } }
    @include respond(tab-port) {
        width: 100%;
        padding-top: 3rem;
        padding-left: 3rem; }


    &__input {
        font-size: $font-size-paragraph;
        letter-spacing: .2rem;
        font-weight: 400;
        color: $color-black;
        padding: 2rem 0rem 2rem 2rem;
        background: $color-white;
        border: 0;
        border-bottom: .3rem solid transparent;
        border-radius: 0.5rem;
        width: 75%;
        @include respond(tab-land) {
            width: 65%; }
        @include respond(tab-port) {
            width: 90%;
            box-shadow: $box-shadow; }


        &:focus {
            outline: none;
            border-bottom: .3rem solid green;
            box-shadow: $box-shadow; }
        &:focus:invalid {
            border-bottom: .3rem solid red; } }

    &__label {
        font-size: $font-size-paragraph;
        letter-spacing: .2rem;
        font-weight: 400;
        display: block;
        margin-top: 1rem;
        margin-left: 1.5rem;
        transition: .2s all; }

    &__input:placeholder-shown + &__label {
        opacity: 0;
        visibility: hidden;
        transform: translateY(-3rem); }

    &__group-radio {
        display: inline-block;

        @include respond(tab-land) {
            display: block;
            &:not(:last-child) {
                margin-bottom: 2rem; } } }



    &__input-radio {
            display: none; }

    &__label-radio {
        cursor: pointer;
        position: relative;
        padding-left: 4rem; }


    &__button-radio {
        height: 3rem;
        width: 3rem;
        border-radius: 50%;
        border: .5rem solid $color-brown-light;
        display: block;
        position: absolute;
        left: 0;
        top: -0.5rem;
        &::after {
            content: "";
            display: block;
            height: 1.5rem;
            width: 1.5rem;
            border-radius: 50%;
            background: $color-brown-light;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            opacity: 0;
            transition: .2s all; } }

    &__input-radio:checked ~ &__label-radio &__button-radio::after {
        opacity: 1; } }
















