.footer {
    padding: 8rem;
    background: #333;
    &__logo {
        width: 12rem; }

    &__list {
        list-style: none;
        text-align: center; }

    &__item {
        display: inline-block;
        &:not(:last-child) {
            margin-right: 5rem; }
        @include respond(tab-land) {
            &:not(:last-child) {
                margin-right: 1.6rem; } }
        @include respond(tab-port) {
            margin: 0 3rem 3rem 0; } }
    &__link {

        &:link, &:visited {
            color: $color-gray;
            text-decoration: none;
            text-transform: uppercase;
            font-size: $font-size-paragraph;
            letter-spacing: .2rem;
            font-weight: 300; }

        &:active {}

        &:hover {
            color: $color-brown-dark; } } }
