//анимация, которая поднимает кнопку на 50 пикселей вверх
@keyframes BtnUp {
    0% {
        opacity: 0;
        transform: translateY(5rem); }

    100% {
        opacity: 1;
        transform: translateY(0); } }

// анимация выплывания текста слева направо
@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }

    80% {
        transform: translateX(1rem); }

    100% {
        opacity: 1;
        transform: translateX(0); } }

// анимация выплывания текста справа налево
@keyframes RightToLeft {
    0% {
        opacity: 0;
        transform: translateX(10rem); }

    80% {
        transform: translateX(-1rem); }

    100% {
        opacity: 1;
        transform: translateX(0); } }

