.feedback-card {
    width: 80%;
    background: rgba($color-white, .75);
    box-shadow: $box-shadow;
    margin: 0 auto;
    padding: 6rem;
    transform: skewX(-25deg);
    @include respond(tab-port) {
        transform: skewX(350deg);
        padding: 3rem; }
    &>* {
        transform: skewX(25deg);
        @include respond(tab-port) {
            transform: skewX(-350deg);
            text-align: center; } }
    &__img {
        width: 15rem;
        height: 15rem;
        background: #9c4545;
        float: left;
        border-radius: 100rem;
        shape-outside: circle(50% at 50% 50%);
        -webkit-shape-outside: circle(50% at 50% 50%);
        margin-right: 4rem;
        overflow: hidden;
        position: relative;
        @include respond(tab-port) {
            float: none;
            margin: 0 auto;
            margin-bottom: 3rem; }
        img {
            height: 100%;
            transform: scale(1.2);
            transition: .5s all; } }

    &__name {
        text-align: center;
        font-size: 2.4rem;
        letter-spacing: .2rem;
        font-weight: 400;
        color: $color-white;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-20%);
        opacity: 0;
        transition: .5s all; }

    &:hover &__name {
        transform: translate(-50%,-50%);
        opacity: 1; }
    &:hover img {
        transform: scale(1);
        filter: blur(.3rem) brightness(60%); } }




